// export default Screenshots;
import { useEffect, useState } from "react";
import { useLocation } from "react-router-dom";
import first from "../../assets/screens/1.png";
import second from "../../assets/screens/2.png";
import third from "../../assets/screens/3.png";
import fourth from "../../assets/screens/4.png";
import fivth from "../../assets/screens/5.png";
import "./style.css";

const Screenshots = () => {
	const [current, setCurrent] = useState(2);
	const [data, setData] = useState([
		first,
		second,
		third,
		fourth,
		fivth,
		first,
		second,
		third,
	]);
	const [touchStart, setTouchStart] = useState(0);
	const [touchEnd, setTouchEnd] = useState(0);

	const location = useLocation();

	useEffect(() => {
		if (location.state?.scrollTo) {
			const element = document.getElementById(location.state.scrollTo);
			if (element) {
				element.scrollIntoView({ behavior: "smooth" });
			}
		}
	}, [location]);

	const prevSlide = () => {
		setCurrent((prev) => (prev === 0 ? data?.length - 1 : prev - 1));
	};

	const nextSlide = () => {
		setCurrent((prev) => (prev === data?.length - 1 ? 0 : prev + 1));
	};

	// Minimum swipe distance (in px) to trigger slide change
	const minSwipeDistance = 50;

	const onTouchStart = (e) => {
		setTouchEnd(0); // Reset touchEnd
		setTouchStart(e.targetTouches[0].clientX);
	};

	const onTouchMove = (e) => {
		setTouchEnd(e.targetTouches[0].clientX);
	};

	const onTouchEnd = () => {
		if (!touchStart || !touchEnd) return;

		const distance = touchStart - touchEnd;
		const isLeftSwipe = distance > minSwipeDistance;
		const isRightSwipe = distance < -minSwipeDistance;

		if (isLeftSwipe) {
			nextSlide();
		}
		if (isRightSwipe) {
			prevSlide();
		}
	};

	const getVisibleSlides = () => {
		const total = data.length;
		const indices = [
			(current - 2 + total) % total,
			(current - 1 + total) % total,
			current,
			(current + 1) % total,
			(current + 2) % total,
		];
		return indices;
	};

	return (
		<div className="screenshots" id="Screenshot">
			<div>
				<div className="custom-swiper">
					<button className="arrow left" onClick={prevSlide}>
						&#10094;
					</button>
					<div
						className="slides"
						onTouchStart={onTouchStart}
						onTouchMove={onTouchMove}
						onTouchEnd={onTouchEnd}>
						{getVisibleSlides().map((index, pos) => {
							const isActive = index === current;
							let extraClass = "";
							if (pos === 0 || pos === 1) extraClass = "left-slide";
							if (pos === 3 || pos === 4) extraClass = "right-slide";
							return (
								<div
									key={index}
									className={`slide ${extraClass} ${isActive ? "active" : ""}`}>
									<div
										className={`phone-wrapper ${
											isActive ? "iphone-frame" : ""
										}`}>
										<img
											src={data[index]}
											draggable="false"
											alt={`screenshot-${index}`}
										/>
									</div>
								</div>
							);
						})}
					</div>
					<button className="arrow right" onClick={nextSlide}>
						&#10095;
					</button>
				</div>
				<div className="dots">
					{data.map((_, i) => (
						<span
							key={i}
							className={`dot ${i === current ? "active" : ""}`}
							onClick={() => setCurrent(i)}
						/>
					))}
				</div>
			</div>
		</div>
	);
};

export default Screenshots;
